@import url('https://fonts.googleapis.com/css?family=Roboto+Mono');

*{
    font-family: 'Roboto Mono', monospace;
    padding: 0;
    margin: 0;
}

.home{
    margin-left: 1%;
    font-size: calc(12px + 1.5vw)
}

ul li a {
    font-size: 1.5vw;
}

.material-icons.md-18 {
    font-size: 40px;

}

.parallax-container {
    height: 100vh;
}

.navbar-fixed{
    position: absolute;
}

.nav-wrapper a {
    margin-right: 5%;
}

.intro-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-top: calc(-50px + -2vw);
      
  }

.intro {
    width: 80vw;
    z-index: 1;
    background-color: white;
    margin-top: -10;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 5vw;
}

.intro h3 {
    text-align: center;
    font-size: calc(18px + 1.4vw);
    margin-top: calc(8px + 1.2vw);
  }

.intro h1 {
    text-align: center;
    font-size: calc(25px + 2.2vw);
    margin-top: 2%;
  }

.intro p {
    text-align: justify;
  }

.portfolio {
    display: grid;
    width: 70vw;
    margin: 5% 0 0 0;
    padding: 0;
    grid-gap: 10vw;
    justify-items: center;
    grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
}

.project{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: stretch;
        justify-content: stretch;
    
}

.project p {
    text-align: center;
}

.profile{
    margin: 5% 0 0 0;
    display: grid;
    grid-template-columns: repeat(2, 50vw);
    overflow: hidden;
}

.info {
    background-color: black;
    height: 45vw;
    color: white;
    padding: 0 5%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.info h3 {
    margin: 1% 0;
    font-size: calc(25px + 2.2vw);
}

.info p {
    font-size: calc(10px + 1.5vw);
}


.profile img {
    height: 45vw;
}

.trabajo-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: calc(-50px + -1.8vw) 0 5% 0;
}

.trabajo {
    width: 80vw;
    z-index: 1;
    background-color: white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 5vw;
}

.trabajo h3 {
    margin: 1% 0 4% 0;
    font-size: calc(25px + 2.2vw);
}

.trabajo p {
    text-align: justify;
}

.video-container {
    margin-top: 5%;
    width: 80vw;
}

.page-footer {
    padding: .5%;
}

.footer-text {
    margin: 0;
    text-align: center;
    font-size: calc(10px + .4vw)
}

  @media only screen and (max-width : 992px) {
    .parallax img {
      max-height: 130%;
      -webkit-transition: height 999999s ease-in-out;
      -o-transition: height 999999s ease-in-out;
      transition: height 999999s ease-in-out;
    }

    .profile {
        grid-template-columns: 100vw;
    }

    .profile .info {
        height: 80vw;
    }

    .profile img {
        height: 80vw;
    }

    .intro {
        width: 90vw;
    }

    .trabajo {
        width: 90vw;
    }

  }
  
  @media only screen and (min-width: 993px) {
    .parallax img {
      max-height: 170%; 
    }
  }

  @media screen and (-webkit-min-device-pixel-ratio:0) and (max-width : 992px){ 
    
    .parallax-container {
        height: 94.15vh;
    }
}



